* {
    box-sizing: border-box;
    padding: 0%;
    margin: 0%;
}
body {
    margin: 0;
    background: linear-gradient(-20deg, #2b5876 0%, #4e4376 100%);
    font-family: 'Montserrat', sans-serif;
    font-size: 130%;
    color:white;
}
.background {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -3000;
}
.background::after {
  content:"";
  position: absolute;
  width:200%;
  height: 200%;
  left: -50%;
  top: -50%;
  background-image: url(data:image/png;base64,);
  animation:grain 1.5s steps(6) infinite;
  opacity:25%;
  z-index: 3000;
}
@keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}
  #spinner{
    visibility: visible;
    width: 10em;
    height: 10em;
    border: 2px solid #f3f3f3;
    border-top:3px solid #1B2735;
    border-radius: 100%;
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right: 0;
    margin: auto;
    z-index: 1000000000;
    animation: spin 1s infinite linear;
  }
  @keyframes spin {
    from {
        transform: rotate(0deg);
    } to {
        transform: rotate(360deg);
    }
  }
  #spinner.show {
    visibility: visible;
  }
  #spinner.hide {
    visibility: hidden;
    display: none;
  }
.next, .live, .nxt-project, .resume-btn {
    padding: 0;
    border: none;
    background: none;
    position:absolute;
    z-index:100000;
    bottom:0;
    right:0;
    text-decoration: none;
    margin: .1em;
    font-size: 4.5rem;
    background-color: transparent;
    color: transparent;
   -webkit-text-stroke-width: 1px;
   -webkit-text-stroke-color: white;  
}
.next{
    padding: .2rem;
    margin-bottom: 2rem;
}
.next:hover, .live:hover, .nxt-project:hover, .resume-btn:hover {
-webkit-text-stroke-width: 0;  
border-bottom-color: transparent;
background-image: linear-gradient(90deg,#06e,#53a); 
background-size: 400% 400%; 
-webkit-background-clip: text;
-webkit-text-fill-color: transparent; 
-moz-background-clip: text;
-moz-text-fill-color: transparent;
animation: backgroundPositionShift 3s ease infinite;
animation-name: backgroundPositionShift;
animation-duration: 3s;
animation-timing-function: ease;
animation-delay: 0s;
animation-iteration-count: infinite;
animation-direction: normal;
animation-fill-mode: none;
animation-play-state: running;
}

