.live, .nxt-project {
    padding: 1rem;
    font-size: 2rem;
    position: relative;
}

@keyframes backgroundPositionShift {
    0% {
        background-position: 0%;
    }
    40% {
        background-position: 100%;
    }
    100% {
        background-position: 0%;
    }
}
.geeks { 
            width: 50vh; 
            height: 1px; 
            background: #fff; 
            position: absolute; 
            top: 0%; 
            left: 50%; 
            transform: translate(-50%, -50%); 
            -webkit-transform: rotate(90deg);
            -moz-transform: rotate(90deg);
            -o-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
             transform: rotate(90deg);
            opacity:25%;
        } 
        .geeks::before { 
            content: ""; 
            position: absolute; 
            top: 0; 
            left: 0; 
            width: 100%; 
            height: 100%; 
            background: green; 
            animation: animate 30s linear infinite; 
        } 
        @keyframes animate { 
            0% { 
                left: 0; 
            } 
  
            50% { 
                left: 100%; 
            } 
  
            0% { 
                left: 0; 
            } 
        } 
 
